import * as React from "react"
import { Link } from "gatsby"
import MainIcon from '../images/logo-light.svg'
import 'font-awesome/css/font-awesome.min.css';


const Header = () => (
  <>
    <header className="header_section">
      <div className="container">
        <nav className="navbar navbar-expand-lg custom_nav-container ">
          <Link to="/" className="navbar-brand">
            <img src={MainIcon} alt="" />
          </Link>
          <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
            <span className=""> </span>
          </button>
          <div className="collapse navbar-collapse" id="navbarSupportedContent">
            <ul className="navbar-nav">
              <li className="nav-item">
                <Link className="nav-link" to="/">Главная</Link>
              </li>
              <li className="nav-item">
                <div className="dropdown5">
                  <span className="dropbtn5">Направления
                    <i className="fa fa-caret-down"></i>
                  </span>
                  <div className="dropdown-content5">
                    <ul className="menu">
                      <li><a href="#">Производство и Торговля</a>
                        <ul>
                          <li><a href="#Fabrica">Швейно-обувная Фабрика</a></li>
                          <li><a href="#Protection">Средства Индивидуальной Защиты</a></li>
                          <li><a href="#Tools">Строительные Инструменты и Материалы</a></li>
                          <li><a href="#Equipment">Строительное Оборудование</a></li>
                          <li><a href="#Sporting-goods">Спортивные Товары</a></li>
                        </ul>
                      </li>
                      <li><a href="#">Строительство и Проектирование</a>
                        <ul>
                          <li><a href="#COMFORT-JAGALAU">Жилищное Строительство</a></li>
                          <li><a href="#Construction-Of-Engineering-Networks">Строительство Инженерных Сетей и Коммуникаций</a></li>
                          <li><a href="#Rental-of-Premises">Аренда Складских Помещений Класса А и С</a></li>
                        </ul>
                      </li>
                      <li><a href="#">Сельское Хозяйство</a>
                        <ul>
                          <li><a href="https://lakefarm.kz/">Разведение КРС "Абердин Ангус" (племенной)</a></li>
                          <li><a href="https://atekagro.kz/">Интенсивный Яблочный Сад</a></li>
                          <li><a href="https://altyn-teke.kz/">Коневодство Органическое Удобрение - Биогумус (Вермикомпост)</a></li>
                          <li><a href="https://freshfish.kz/">Рыбоводство</a></li>
                          <li><a href="https://premiumlamb.kz/">Разведение МРС "Романовские Овцы"</a></li>
                        </ul>
                      </li>
                    </ul>
                    <a href="#">Аренда VIP Авто</a>
                    <a href="#">Кинематография</a>
                  </div>
                </div>
              </li>
              <li className="nav-item">
                <Link className="nav-link" to="/Магазин">Магазин</Link>
              </li>
              <li className="nav-item">
                <Link className="nav-link" to="/about-us">Карьера</Link>
              </li>
              <li className="nav-item">
                <Link className="nav-link" to="/contacts">Контакты</Link>
              </li>
            </ul>
          </div>
        </nav>
      </div>
    </header>
  </>
)

export default Header